.todo-list {
	margin: 0;
	padding: 0;
	list-style: none;
  }
  
  .todo-list li {
	position: relative;
	font-size: 24px;
	border-bottom: 1px solid #ededed;
  }
  
  .todo-list li:last-child {
	border-bottom: none;
  }
  
  .todo-list li.completed label .description {
	color: #cdcdcd;
	text-decoration: line-through;
  }
  

