.view {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 85px 15px 60px;
  }
  
  .toggle {
    width: 40px;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    cursor: pointer;
    opacity: 0;
  }
  
  .toggle + label {
    background-position: center left;
    background-repeat: no-repeat;
  }
  
  .description {
    word-break: break-word;
    line-height: 1.2;
    font-weight: 400;
    color: #4d4d4d;
    transition: color 0.4s;
  }
  
  .icon {
    position: absolute;
    width: 30px;
    height: 40px;
    font-size: 34px;
    color: #cc9a9a;
    transition: color 0.2s ease-out;
    cursor: pointer;
  }
  
  .icon:hover {
    color: #af5b5e;
  }
  