.footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 10px 15px;
	height: 20px;
	font-size: 15px;
	border-top: 1px solid #e6e6e6;
	color: gray;
  }
  
  .todo-count {
	text-align: left;
  }
  
  .clear-completed {
	text-decoration: none;
	cursor: pointer;
  }
  
  .clear-completed:hover {
	text-decoration: underline;
  }
  